import {Component} from '@angular/core';
import {NavController} from 'ionic-angular';
import {WeatherProvider} from "../../providers/weather/weather";
import {Storage} from "@ionic/storage";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  weather = {description: '', tempC: 0, icon: ''};
  city: string;
  location: string;
  icon_url: string;

  constructor(public navCtrl: NavController, private weatherProvider: WeatherProvider, private storage: Storage) {
  }

  ionViewWillEnter() {
    this.storage.get('city').then((val) => {
      if (val != null) {
        this.city = val;
      } else {
        this.city = 'London';
      }

      this.weatherProvider.getWeather(this.city).subscribe((res: Weather) => {
        console.log(res);
        this.location = res.name;
        this.weather = res.weather[0];
        const tempF = +res.main.temp;
        console.log(this.weather.description);
        this.weather.tempC = Math.round((tempF - 273.15) * 100) / 100;
        this.icon_url = 'https://openweathermap.org/img/w/' + this.weather.icon + '.png'
      });
    });


  }

}

export class Weather {
  constructor(public name: string,public  weather: {description: '', tempC: '', icon: ''},public main: any) {
  }
}



